import React from "react"
import { BrowserRouter, Route, Switch } from "react-router-dom"
import Plantilla from "./plantillas/plantilla-general"

//import Ingreso from "./paginas/ingreso"
import Inicio from "./paginas/inicio"
import Prestamos from "./paginas/prestamos"
import Registrar from "./paginas/registrar-pagos"
import DatosPersonlaes from "./paginas/datos-personales"

function aplicacion() {
  return (
    <BrowserRouter>
      <Plantilla>
        <Switch>
          <Route exact path="/" component={Inicio} />
          <Route exact path="/prestamos" component={Prestamos} />
          <Route exact path="/registrar-pagos" component={Registrar} />
          <Route exact path="/datos-personales" component={DatosPersonlaes} />
        </Switch>
      </Plantilla>
    </BrowserRouter>
  )
}

export default aplicacion
