import React from "react"
import { Link } from "react-router-dom"

function NavBar() {
  function abrirMenu() {
    document.getElementsByTagName("nav")[0].classList.toggle("oculto")
  }

  function handleCheck(e) {
    const botones = document.querySelectorAll("nav a")
    for (let boton of botones) {
      boton.parentElement.className =
        e.currentTarget.dataset.botid === boton.dataset.botid ? "actual" : ""
    }
  }

  return (
    <nav className="oculto">
      <div id="bot-desplegar" onClick={abrirMenu}>
        Desplegar
      </div>
      <ul>
        <li className="actual">
          <Link
            to=""
            data-botid="boton-inicio"
            onClick={handleCheck.bind(this)}
          >
            Saltar a inicio
          </Link>
        </li>
        <li>
          <Link
            to="prestamos"
            data-botid="boton-prestamos"
            onClick={handleCheck.bind(this)}
          >
            Prestamos
          </Link>
        </li>
        <li>
          <Link
            to="registrar-pagos"
            data-botid="boton-pagos"
            onClick={handleCheck.bind(this)}
          >
            Registrar pagos
          </Link>
        </li>
        <li>
          <Link
            to="datos-personales"
            data-botid="boton-datos-personles"
            onClick={handleCheck.bind(this)}
          >
            Datos Personales
          </Link>
        </li>
      </ul>
    </nav>
  )
}

export default NavBar
