import React from "react"
import datos from "../../mocks/prestamos"

let contadorTablas = 0
let contadorCuotas = 0

class ListaPrestamos extends React.Component {
  render() {
    return (
      <React.Fragment>
        {datos.map(dato => {
          return (
            <div className="tabla-prestamo" key={contadorTablas++ + "tabla"}>
              <div className="contenedor-tabla">
                <table>
                  <caption>
                    Préstamo <span>{dato.prestamoNumero}</span>{" "}
                  </caption>
                  <thead>
                    <tr>
                      <th>Cuota</th>
                      <th>Monto</th>
                      <th>Interés</th>
                      <th>Total</th>
                      <th>Pagado</th>
                      <th>Por pagar</th>
                    </tr>
                  </thead>
                  {dato.movimientos.map(movimiento => {
                    return movimiento.numero === 0
                      ? primeraLinea(movimiento, contadorCuotas++)
                      : lineaNormal(movimiento, contadorCuotas++)
                  })}
                </table>
              </div>
            </div>
          )
        })}
      </React.Fragment>
    )
  }
}

export default ListaPrestamos

function primeraLinea(datos, contador) {
  return (
    <tbody key={contador + "celda"}>
      <tr className="fecha">
        <td colSpan="6">{datos.fecha}</td>
      </tr>
      <tr className="desembolso">
        <td>0</td>
        <td colSpan="4">{datos.nota}</td>
        <td>{datos.valor}</td>
      </tr>
    </tbody>
  )
}

function lineaNormal(datos, contador) {
  return (
    <tbody key={contador + "celda"}>
      <tr className="fecha" key={contador + "celda"}>
        <td colSpan="6">{datos.fecha}</td>
      </tr>
      <tr key={++contador + "celda"}>
        <td>{datos.numero}</td>
        <td>{datos.monto}</td>
        <td>{datos.interes}</td>
        <td>{datos.total}</td>
        <td>{datos.pagado}</td>
        <td>{datos.porPagar}</td>
      </tr>
    </tbody>
  )
}
