import React from "react"
import datos from "../../mocks/facturas"
import factura01 from "../../img/fotos-facturas/factura01.jpg"

let contador = 0

function clases(enAnalisis) {
  let laClase = "registro-entrada"
  laClase += enAnalisis ? " en-analisis" : ""
  return laClase
}

class RegistrarPagosLista extends React.Component {
  render() {
    return (
      <div className="tabla-registros">
        {datos.map(dato => {
          return (
            <div className={clases(dato.estado !== 0)} key={contador++}>
              {dato.estado !== 0 ? (
                <span className="en-analisis">En análisis</span>
              ) : (
                ""
              )}
              <div className="factura">
                <img src={factura01} alt="factura" width="140" />
              </div>
              <div className="info">
                <span className="fecha">{dato.fecha}</span>
                <span className="valor">{dato.valor}</span>
                <span className="motivo">
                  <ul>
                    {dato.motivo.map(elMotivo => {
                      return <li key={contador++}>{elMotivo}</li>
                    })}
                  </ul>
                </span>
                <span className="notas">{dato.nota}</span>
              </div>
            </div>
          )
        })}
      </div>
    )
  }
}

export default RegistrarPagosLista
