import React from "react"
import datos from "../../mocks/movimientos"

let marcador = 0
let marcadorInterno = 0

class inicio_datos extends React.Component {
  render() {
    return (
      <section id="tabla-estados">
        {datos.map(dato => {
          return (
            <div className="fecha" key={marcador++}>
              <div className="fecha-ingreso">{dato.fecha}</div>
              {dato.entradas.map(entrada => {
                return (
                  <div
                    className="ingreso"
                    key={`${marcador}-${marcadorInterno++}`}
                  >
                    <div className="informacion">
                      <div className="motivo">{entrada.motivo}</div>
                      <div className="nota">{entrada.nota}</div>
                    </div>
                    <div className="valor negativo">{entrada.valor}</div>
                  </div>
                )
              })}
            </div>
          )
        })}
      </section>
    )
  }
}

export default inicio_datos